body{display:flex;flex-direction:column;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}html,body{width:100%;height:100%;padding:0;margin:0;background-size:cover;background-color:whitesmoke}

.app{display:grid;grid-template-rows:auto;grid-template-columns:1fr;height:100vh}.header{grid-row-start:1;grid-row-end:2;position:relative;display:flex;align-items:center;justify-content:flex-end;background:#ff037d;color:#fff;height:60px;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}.main{grid-row-start:2;grid-row-end:23;overflow:auto}.footer{grid-row-start:23;grid-row-end:24;position:relative;display:flex;align-items:center;justify-content:center;height:20px;box-shadow:0 -5px 6px rgba(0,0,0,0.01);font-family:cursive;color:grey}


@-webkit-keyframes shrink-grow{0%{-webkit-transform:scale(0.95);transform:scale(0.95)}50%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0.95);transform:scale(0.95)}50%{color:#0d47a1;border-color:#0d47a1;fill:#0d47a1}}@keyframes shrink-grow{0%{-webkit-transform:scale(0.95);transform:scale(0.95)}50%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0.95);transform:scale(0.95)}50%{color:#0d47a1;border-color:#0d47a1;fill:#0d47a1}}.tile{position:relative;display:inline-flex;justify-content:center;align-items:center;cursor:pointer;flex-shrink:0;transition:box-shadow .3s, background-color .3s, border-color .3s, -webkit-transform .3s;transition:box-shadow .3s, background-color .3s, transform .3s, border-color .3s;transition:box-shadow .3s, background-color .3s, transform .3s, border-color .3s, -webkit-transform .3s;width:230px;height:230px;animation:fade-in .8s;-webkit-animation:fade-in .8s}.tile.unselected{border:4px dashed #b0b0b0;border-radius:14px}.tile.animated{-webkit-animation:fade-in .8s, shrink-grow 2s ease-out infinite, pink-highlight 2s ease-out infinite;animation:fade-in .8s, shrink-grow 2s ease-out infinite, pink-highlight 2s ease-out infinite;background-color:rgba(255,255,255,0.7);box-shadow:0 4px 16px rgba(0,0,0,0.12)}.tile .preview{display:flex;align-items:center;justify-content:center;overflow:hidden;width:236px;height:236px;margin-left:-5px;margin-top:-13px;-webkit-animation:fade-in .4s;animation:fade-in .4s}.tile .preview .image{position:relative;z-index:2;opacity:1;height:100%;height:236px}.tile .tileFrame{right:16px;width:100%;position:absolute;z-index:1}.tile .tileFrame .frame{display:flex;justify-content:center;overflow:hidden;height:264px;-webkit-animation:fade-in .4s;animation:fade-in .4s}.add-action{border:none !important;width:230px;height:230px;border-radius:14px}.add-icon{font-size:4.5rem !important;color:#ff037d}.action-buttons{position:absolute;z-index:99999;top:20px;left:14px}.action-buttons button{margin:4px;height:80px}

.searchBar{margin:10px 20px 10px 20px}.searchBar input{font-family:cursive;font-weight:lighter}.gridItem{-webkit-animation:fadein 3s;animation:fadein 3s}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}

